body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-1:#2497cf;
  --color-1-lighter:#9fdefd;
  --color-2:#2441cf;
  --color-3:#5c24cf;
  --color-4:#b224cf;
  --color-5:#cf2497;
  --color-6:#cf2441;
  background: #2441cf;
  background: var(--color-2)
}
h1 {
  color: white;
}
.App {
  text-align: center;
  font-family: 'Courier New', Courier, monospace
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.year {
    color:white;
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
}

.calendar {
    background-color: var(--color-1);
    border-radius: 10px;
    padding: 5%;
    margin-left:10%;
    margin-right: 10%;
    margin-bottom: 10%;
}
.dates {
    border-radius: 15px;
    background: whitesmoke;
    border: solid;
    border-color:white;
    border-width: 10px;

}
.dates_table {
    border-collapse: collapse;
    width: 100%;
    height: 500px;
    
}

.date_row {
    display: inline-block;
}
.border_right {
    border-right:solid;
    border-color: white;
}
.border_bottom {
    border-bottom: solid;
    border-color: white;
}
.date_entry {
    text-align: center;
    padding: 0%;
    width:100px;
}
.date_entry.locked {
    opacity: 0.2;
}

.reserved {
    background: yellow;
}

.selected {
    background: var(--color-1);
}

.date_entry:hover:not(.reserved) {
    background: var(--color-1);
}

.date {
    height:75%;
}

.reserve_button {
    margin: 2%;
    display: block;
    float: right;
    width: 20%;
    height: 25px;
    background:whitesmoke;
    border:0px;
}

.reserve_button:hover {
    background: var(--color-1-lighter)
}

.reserve_button:active {
    background: var(--color-2);
}

.text_input {
    display: block;
    background:none;
    text-align: left;
    margin-left: 20%;
    margin-right: 20%;
    padding-bottom: 2%;

}

.text_input_label {
    display: block;
    font-size: 23px;
    
}

.text_input_textbox {
    font-size: 23px;
    display: block;
    border: none;
    height: 25px;
    padding: 2%;
    width: 100%;
    border-radius: 10px;
}
.button {
    display: flex;
    padding: 5px;
    background: var(--color-1);
    border: none;
    border-radius: 10px;
    font-size: 23px;
}
.modal_box {
    position: fixed;
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    top:0;
    left:0;
}
.modal {
    position: fixed;
    top: 10%;
    left: 25%;
    right:25%;
    background:var(--color-1-lighter);
    border-radius: 10px;
    padding: 5%;
}
.modal_open {
    display: block;
}

.modal_closed {
    display: none;
}

.modal_heading {
    text-align: center;
    margin-bottom:10%;
}
.modal button {
    display: block;
    margin-left: 80%;
}

.close_icon {
    display: block;
    float:right;
    font-size: 20px;
}

.close_icon:hover {
    cursor: pointer;
}
